import React from "react";
import {Box, Chip, Container, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import {HeroCounterPackGrid, HeroCounterHeroGrid, HeroGrid, HeroPackSelected} from "./index";

interface IProps {
    heroIds: number[],
    currentMode: number
}

interface IState {
    heroIds: number[],
    heroLostProbs: Map<number, number>,
    heroCounterProbs: Map<number, number>,
    heroAttackerProbs: Map<number, number>,
    heroLostPacks: number[][],
    heroWinPacks: number[][],
    heroWinProbs: Map<number,number>
    heroCounterFilter: number[],
    heroAttackerFilter: number[],
    requestsInProgress: number,
    currentMode: number
}

export class HeroCounterPackMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            heroIds: props.heroIds ? props.heroIds : [],
            heroLostPacks: [],
            heroCounterFilter: [],
            heroWinPacks: [],
            heroAttackerFilter: [],
            requestsInProgress: 0,
            heroLostProbs: new Map<number,number>(),
            heroWinProbs: new Map<number,number>(),
            heroCounterProbs: new Map<number,number>(),
            heroAttackerProbs: new Map<number,number>(),
            currentMode: props.currentMode
        };
        this.requestsInProgress=0
        this.currentMode=props.currentMode
        this.selectHero = this.selectHero.bind(this)
        this.deselectHero = this.deselectHero.bind(this)
        this.filterCounterHero = this.filterCounterHero.bind(this)
        this.filterAttackerHero = this.filterAttackerHero.bind(this)
        this.selectMode=this.selectMode.bind(this)
        this.updateHeroIds(this.state.heroIds)
    }
    currentMode: number
    requestsInProgress: number
    initialized: boolean = false
    requestSeq=0


    getPacksData(heroIds: number[]) {
        const currentMode=this.state.currentMode
        const requestId=this.requestSeq++
        const heroParams=[-1,-1,-1,-1,-1]
        for (let i=0;i<5 && i<heroIds.length;i++) {heroParams[i]=heroIds[i];}
        this.requestsInProgress++
        this.setState({requestsInProgress:this.requestsInProgress})
        let url=""
        if (currentMode===0) {
            url="https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findLostPackHeroes/"
        } else {
            url="https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findWinPackHeroes/"
        }
        fetch(url+heroParams.join("/"), {
            method: "GET"
        }).then(res => res.json())
            .then(
                (result) => {
                    if (result['items']) {
                        const heroLostPacks = []
                        let cnt = 0
                        const heroProbs = new Map<number, number>()
                        for (const x in result['items']) {
                            const z = result['items'][x]
                            let lostPack = []
                            if (currentMode === 0) {
                                lostPack = [~~z.a1, ~~z.a2, ~~z.a3, ~~z.a4, ~~z.a5]
                            } else {
                                lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]
                            }
                            for (const heroId of lostPack) {
                                let m = heroProbs.get(~~heroId)
                                if (m) {
                                    m++
                                } else {
                                    m = 1
                                }
                                heroProbs.set(~~heroId, m)

                            }
                            cnt++
                            lostPack.push(Number.parseFloat(z.r))
                            heroLostPacks.push(lostPack)
                        }
                        for (const heroPackId of Array.from(heroProbs.keys())) {
                            const mmd = heroProbs.get(~~heroPackId)
                            if (mmd && cnt > 0) heroProbs.set(~~heroPackId, 100 * mmd / cnt)
                        }
                        if (currentMode===0) {
                            this.setState(
                                {heroIds: heroIds, heroCounterProbs: heroProbs, heroLostPacks: heroLostPacks}
                            )
                        } else {
                            this.setState(
                                {heroIds: heroIds, heroAttackerProbs: heroProbs, heroWinPacks: heroLostPacks}
                            )
                        }
                    }
                }).finally(()=>{
                    this.requestsInProgress--
                    this.setState({requestsInProgress:this.requestsInProgress})
        })
    }

    updateHeroIds(heroIds: number[]) {
        const currentMode=this.currentMode
        this.initialized=true
        const requestId=this.requestSeq++
            this.setState(
                {heroIds: heroIds, heroLostProbs: new Map<number, number>(), heroLostPacks: [], heroWinProbs: new Map<number, number>(), heroWinPacks: [],heroCounterProbs:new Map<number, number>(),heroAttackerProbs:new Map<number,number>()}
            )
        if (heroIds.length == 5) {
            this.getPacksData(heroIds)
        } else {
            const heroParams=[-1,-1,-1,-1,-1]
            for (let i=0;i<5 && i<heroIds.length;i++) {heroParams[i]=heroIds[i];}
            console.log('RIP+1',requestId,this.state.requestsInProgress)
            this.requestsInProgress++
            this.setState({requestsInProgress:this.requestsInProgress})
            let url=""
            if (currentMode===0) {
                url="https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findLostPackHeroesStat/"
            } else {
                url="https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findWinPackHeroesStat/"
            }
            fetch(url+heroParams.join("/"), {
                method: "GET"
            })
                .then(res => res.json())
                .then(
                    (result) => {
                        console.log("CS-S",requestId,this.currentMode,currentMode)
                            let heroProbs = new Map<number, number>()
                            if (result['items']) {
                                for (const j in result['items']) {
                                    if (result['items'][j]['d']) {
                                        heroProbs.set(~~result['items'][j]['d'], Number.parseFloat(result['items'][j]['prob']))
                                    }
                                }
                            }
            if (currentMode===0) {
                this.setState({
                    heroLostProbs: heroProbs,
                    heroIds: heroIds,
                    heroLostPacks: []
                });
            } else {
                this.setState({
                    heroWinProbs: heroProbs,
                    heroIds: heroIds,
                    heroWinPacks: []
                });
            }
                    },
                    // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
                    // чтобы не перехватывать исключения из ошибок в самих компонентах.
                    (error) => {
                        console.error("error req",requestId,error)
                        if (currentMode===0) {
                            this.setState({
                                heroLostProbs: new Map<number, number>(),
                                heroIds: heroIds,
                                heroLostPacks: []
                            });
                        } else {
                            this.setState({
                                heroWinProbs: new Map<number, number>(),
                                heroIds: heroIds,
                                heroWinPacks: []
                            });
                        }
                    }
                ).finally(()=>{
                    console.log('ReqFin ',requestId)
                this.requestsInProgress--
                this.setState({requestsInProgress:this.requestsInProgress})
            })
        }
    }

    filterCounterHero(e: any) {
       // console.log('HF0',e.target)
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroCounterFilter = this.state.heroCounterFilter
            const index = heroCounterFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroCounterFilter.splice(index, 1);
                this.setState({heroCounterFilter: heroCounterFilter})
            } else {
                heroCounterFilter.push(heroId)
                this.setState({heroCounterFilter: heroCounterFilter})
            }
        }
    }

    filterAttackerHero(e: any) {
        // console.log('HF0',e.target)
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroAttackerFilter = this.state.heroAttackerFilter
            const index = heroAttackerFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroAttackerFilter.splice(index, 1);
            } else {
                heroAttackerFilter.push(heroId)
            }
            this.setState({heroAttackerFilter: heroAttackerFilter})
        }
    }

    selectHero(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIds
            if (!heroIds.includes(heroId) && heroIds.length < 5) {
                heroIds.push(heroId)
                this.updateHeroIds(heroIds)
            }
        }
    }

    deselectHero(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIds
            const index = heroIds.indexOf(heroId, 0)
            if (index > -1) {
                heroIds.splice(index, 1);
                this.updateHeroIds(heroIds)
            }
        }
    }

    selectMode(e:any,value:string) {
        const newMode=~~value
        this.currentMode=newMode
        console.log('Set mode ',newMode)
        this.setState({currentMode:newMode})
        this.updateHeroIds(this.state.heroIds)
    }

    render() {
            if (this.state.heroIds.length == 5) {
               /* if (!this.initialized) {
                    this.updateHeroIds(this.state.heroIds)
                }*/
                return (
                    <Container sx={{
                        bgcolor: 'background.default',
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "flex-start",
                        alignItems: "center",
                    }}>
                        <ToggleButtonGroup
                            color="secondary"
                            value={""+this.currentMode}
                            exclusive
                            onChange={this.selectMode}
                        >
                            <ToggleButton value="0" color="primary">Counter-pack</ToggleButton>
                            <ToggleButton value="1" color="primary">Attack-pack</ToggleButton>
                        </ToggleButtonGroup>
                        <Divider><Chip label={this.state.requestsInProgress === 0?"Selected pack":"Loading...just wait! ("+this.state.requestsInProgress+")"}/></Divider>
                        <HeroPackSelected heroIds={this.state.heroIds} deselectHero={this.deselectHero}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?(this.currentMode===0?"Counter heroes - fill free to filter packs":"Sucked heroes - fill free to filter packs"):"Loading...just wait!("+this.state.requestsInProgress+")"}/></Divider>
                        <HeroCounterHeroGrid heroProbs={this.currentMode===0?this.state.heroCounterProbs:this.state.heroAttackerProbs}
                                             onFilterCounterHero={this.currentMode===0?this.filterCounterHero:this.filterAttackerHero}
                                             heroCounterFilter={this.currentMode===0?this.state.heroCounterFilter:this.state.heroAttackerFilter} key={10+this.currentMode}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?(this.currentMode===0?"Counter hero packs":"Sucked defenders hero packs"):"Loading...just wait! ("+this.state.requestsInProgress+")"}/></Divider>
                        <HeroCounterPackGrid heroLostPacks={this.currentMode===0?this.state.heroLostPacks:this.state.heroWinPacks}
                                             onFilterCounterHero={this.currentMode===0?this.filterCounterHero:this.filterAttackerHero}
                                             heroCounterFilter={this.currentMode===0?this.state.heroCounterFilter:this.state.heroAttackerFilter} key={20+this.currentMode}/>
                    </Container>
                )
            } else {
                if (!this.initialized) {
                    this.updateHeroIds(this.state.heroIds)
                }
                return (

                    <Container sx={{
                        //width: '100%',
                        m: 0.25,
                        p: 0.25,
                        bgcolor: 'background.default',
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "flex-start",
                        alignItems: "center",
                    }}>
                        <ToggleButtonGroup
                            color="secondary"
                            value={""+this.currentMode}
                            exclusive
                            onChange={this.selectMode}
                        >
                            <ToggleButton value="0" color="primary">Counter-pack</ToggleButton>
                            <ToggleButton value="1" color="primary">Attack-pack</ToggleButton>
                        </ToggleButtonGroup>
                        <HeroPackSelected heroIds={this.state.heroIds} deselectHero={this.deselectHero}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?(this.currentMode===0?"Choose pack to attack":"Choose pack of winners"):"Loading...just wait!("+this.state.requestsInProgress+")"}/></Divider>
                        <Box sx={{
                            m: 0,
                            p: 0
                        }}>
                            <HeroGrid heroIds={this.state.heroIds} heroProbs={this.currentMode===0?this.state.heroLostProbs:this.state.heroWinProbs}
                                      selectHero={this.selectHero} key={30+this.currentMode}/>
                        </Box>
                    </Container>
                )
            }
    }
}

export default HeroCounterPackMode;