import React from "react";
import {HeroGrid, HeroPackSelected} from "./index";
import {Box, Button, Chip, Container, Divider} from "@mui/material";
import {HeroData} from "../types";

interface IQueueItem {
    heroIdsA: number[],
    heroIdsD: number[]
}

interface IProps {
    heroIdsA: number[],
    heroIdsD: number[],
    navigateFunction: any
}

interface IState {
    heroIdsA: number[],
    heroIdsD: number[],
    displayEditA: boolean,
    displayEditD: boolean,
    requestsInProgress: number
}

export class HeroAddToTrainMode extends React.Component<IProps, IState> {
    static emptyMap=new Map<number,number>()
    constructor(props:any) {
        super(props);
        this.state = {
            heroIdsA: props.heroIdsA ? props.heroIdsA : [],
            heroIdsD: props.heroIdsD ? props.heroIdsD : [],
            displayEditA: props.heroIdsA.length===5,
            displayEditD: props.heroIdsD.length===5,
            requestsInProgress: 0
        };
        this.selectHeroA=this.selectHeroA.bind(this)
        this.deselectHeroA=this.deselectHeroA.bind(this)
        this.selectHeroD=this.selectHeroD.bind(this)
        this.deselectHeroD=this.deselectHeroD.bind(this)
        this.toggleEditA=this.toggleEditA.bind(this)
        this.toggleEditD=this.toggleEditD.bind(this)
        this.sendToQueue=this.sendToQueue.bind(this)
    }

    sendToQueue(e:any) {
        const body:any={
            a1:-1,
            a2:-1,
            a3:-1,
            a4:-1,
            a5:-1,
            d1:-1,
            d2:-1,
            d3:-1,
            d4:-1,
            d5:-1
        }
        for (let i=0;i<5 && i<this.state.heroIdsA.length;i++) {body["a"+(i+1)]=this.state.heroIdsA[i];}
        for (let i=0;i<5 && i<this.state.heroIdsA.length;i++) {body["d"+(i+1)]=this.state.heroIdsD[i];}
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch("https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/queue/",{
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(body)}).then(()=>{
                console.log("Good")
                this.props.navigateFunction(2)
        }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    selectHeroA(e:any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIdsA
            if (!heroIds.includes(heroId) && heroIds.length < 5) {
                heroIds.push(heroId)
                this.setState({heroIdsA:heroIds })
            }
        }
    }
    selectHeroD(e:any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIdsD
            if (!heroIds.includes(heroId) && heroIds.length < 5) {
                heroIds.push(heroId)
                this.setState({heroIdsD:heroIds})
            }
        }
    }

    deselectHeroA(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIdsA
            const index = heroIds.indexOf(heroId, 0)
            if (index > -1) {
                heroIds.splice(index, 1);
                this.setState({heroIdsA:heroIds})
            }
        }
    }

    deselectHeroD(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIdsD
            const index = heroIds.indexOf(heroId, 0)
            if (index > -1) {
                heroIds.splice(index, 1);
                this.setState({heroIdsD:heroIds})
            }
        }
    }

    toggleEditA(e:any) {
        const newState=!this.state.displayEditA
        this.setState({displayEditA:newState})
    }

    toggleEditD(e:any) {
        const newState=!this.state.displayEditD
        this.setState({displayEditD:newState})
    }

    renderA() {
        if (this.state.displayEditA) {
            return (<Box className={"heroADBox"}><Divider><Chip label={this.state.requestsInProgress>0?"Loading..Just wait!":"Attacker Team"} /></Divider>
                    <HeroPackSelected heroIds={this.state.heroIdsA} deselectHero={this.deselectHeroA}/>
                    {this.state.heroIdsA.length>0?
                        <Button variant="outlined"  className={"heroEditPackButton"} onClick={this.toggleEditA}>Done</Button>
                        :
                        null}
                <Divider><Chip
                        label={this.state.requestsInProgress > 0 ? "Loading..Just wait!" : "Choose Attacker heroes"}/></Divider>
                    <HeroGrid heroIds={this.state.heroIdsA} heroProbs={HeroAddToTrainMode.emptyMap}
                              selectHero={this.selectHeroA}/>
            </Box>)
        } else {
            return (<Box className={"heroADBox"}><Divider><Chip label={this.state.requestsInProgress>0?"Loading..Just wait!":"Attacker Team"} /></Divider>

                    <HeroPackSelected heroIds={this.state.heroIdsA} deselectHero={this.deselectHeroA}/>
                        <Button variant="outlined"  className={"heroEditPackButton"} onClick={this.toggleEditA}>Edit</Button>
            </Box>)
        }
    }



    renderB() {
        if (this.state.displayEditD) {
            return (<Box  className={"heroADBox"}>
                <Divider><Chip label={this.state.requestsInProgress>0?"Loading..Just wait!":"Defender Team"} /></Divider>
                    <HeroPackSelected heroIds={this.state.heroIdsD} deselectHero={this.deselectHeroD}/>
                    {this.state.heroIdsD.length>0?
                        <Button variant="outlined" className={"heroEditPackButton"} onClick={this.toggleEditD}>Done</Button>
                        :
                        null}
                <Divider><Chip label={this.state.requestsInProgress>0?"Loading..Just wait!":"Choose Defender heroes"} /></Divider>
                <HeroGrid heroIds={this.state.heroIdsD} heroProbs={HeroAddToTrainMode.emptyMap}
                          selectHero={this.selectHeroD}/>
            </Box>)
        } else {
            return (<Box  className={"heroADBox"}>
                <Divider><Chip label={this.state.requestsInProgress>0?"Loading..Just wait!":"Defender Team"} /></Divider>
                    <HeroPackSelected heroIds={this.state.heroIdsD} deselectHero={this.deselectHeroD}/>
                    <Button variant="outlined" className={"heroEditPackButton"} onClick={this.toggleEditD}>Edit</Button>
            </Box>)
        }
    }
    renderSendButton() {
        if (this.state.heroIdsA.length>0 && this.state.heroIdsD.length>0 && !this.state.displayEditA && !this.state.displayEditD) {
            return (<Button variant="contained" onClick={this.sendToQueue} sx={{
                marginTop: '1em',
                maxWidth: 'min-content'
            }}>Send</Button>)
        } else {
            return (<Button disabled>Choose heroes before send</Button>)
        }
    }

    render() {
        return (<Box sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center'
        }}>
            {this.renderA()}
            {this.renderB()}
            {this.renderSendButton()}
        </Box>)
    }
}

export default HeroAddToTrainMode;