import React from "react";
import {HeroCounterPackMode} from "./index";
import {HeroFightQueueMode} from "./heroFightQueueMode";
import {Box, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import HeroAddToTrainMode from "./heroAddToTrainMode";
import HeroOneFightMode from "./heroOneFightMode";
import HeroRouletteMode from "./heroRouletteMode";

interface IProps {
    currentMode: number
}

interface IState {
    currentMode: number
}

export class HeroModeSelect extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            currentMode: props.currentMode
        }
        this.setMode=this.setMode.bind(this)
        this.navigateToMode=this.navigateToMode.bind(this)
    }
    navigateToMode(newMode:number) {
        this.setState({currentMode: ~~newMode})
    }
    setMode(e:any,value:string) {
            this.setState({currentMode: ~~value})
    }
    renderMode(currentMode:number) {
        if (currentMode===0) {
            return (<HeroCounterPackMode heroIds={[]} currentMode={0}/>)
        } else if (currentMode===2) {
            return (<HeroOneFightMode navigateFunction={this.navigateToMode} currentMode={1}/>)
        } else if (currentMode===3) {
            return (<HeroRouletteMode navigateFunction={this.navigateToMode} currentMode={0}/>)
        } else {
            return (null)
        }
    }
    render() {
        return (
            <Box sx={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "flex-start",
                alignItems: "center",
            }}>
            <ToggleButtonGroup
                color="secondary"
                value={""+this.state.currentMode}
                exclusive
                onChange={this.setMode}
            >
                <ToggleButton value="0" color="primary">Find pack</ToggleButton>
                <ToggleButton value="2" color="primary">One fight</ToggleButton>
                <ToggleButton value="3" color="primary">Roulette</ToggleButton>
                    </ToggleButtonGroup>
                <Divider />
                {this.renderMode(this.state.currentMode)}
            </Box>
        )
    }
}

export default HeroModeSelect